.map-container {
  text-align: center;
  max-width: 1200px;
  margin: auto;
}

.race-map {
  width: 100%;
}

.map-row {
  justify-content: center;
  display: flex;
}

.county-path {
  stroke: #0003;
  fill: #ffffff1a;
  stroke-width: .5px;
}

.choropleth .county-path {
  stroke: #000;
}

.faded {
  opacity: .15 !important;
}

.highlighted {
  fill: #333;
}

.county-path:focus, .spike:focus {
  outline: none;
}

.legend-container {
  justify-content: center;
  align-items: stretch;
  margin: auto;
  display: flex;
}

.color-scale-legend {
  width: 350px;
}

.legend-text {
  fill: #ffffffbf;
}

.legend-avg-line {
  stroke-dasharray: 1;
  stroke: #eee;
  stroke-width: 1px;
}

.legend-avg-label {
  fill: #eee;
  font-size: 9px;
}

.spike-legend {
  min-width: 135px;
}

.city-label {
  pointer-events: none;
  text-shadow: 1px 0 #fff, -1px 0 #fff, 0 1px #fff, 0 -1px #fff, 1px 1px #fff, -1px -1px #fff, 1px -1px #fff, -1px 1px #fff;
  font-size: 12px;
}

.city-label-dot {
  pointer-events: none;
}
